import React from "react";
import Card from "../components/Card";
//import {AppContext} from "../App";

function Home({items, searchValue, setSearchValue,
                  onSearchInp,onAddToCart, onFavorite, isReady}){

    //const {getAddedItems} = React.useContext(AppContext);
    //console.log(getAddedItems)
    const renderItems = () => {
        const filteredItems = items.filter((item) =>
            item.name.toLowerCase().includes(searchValue),
        );
        return (
            isReady ? [...Array(8)] : filteredItems).map((item, index) => (
                <Card
                    key={index}
                    onFav={(obj)=> onFavorite(obj)}
                    onPlus={onAddToCart}
                    //added={getAddedItems(item && item.id)}
                    {...item}
                    loading={isReady}
                />
            ));
    }
    return(
            <section className='products'>
                <div className='container'>
                    <div className='topSection'>
                        <h3>{searchValue ? `Поиск по запросу: "${searchValue}"` : `Все кроссовки`}</h3>
                        <div className='search'>
                            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M15.25 15.25L11.8855 11.8795L15.25 15.25ZM13.75 7.375C13.75 9.06576 13.0784 10.6873 11.8828 11.8828C10.6873 13.0784 9.06576 13.75 7.375 13.75C5.68424 13.75 4.06274 13.0784 2.86719 11.8828C1.67165 10.6873 1 9.06576 1 7.375C1 5.68424 1.67165 4.06274 2.86719 2.86719C4.06274 1.67165 5.68424 1 7.375 1C9.06576 1 10.6873 1.67165 11.8828 2.86719C13.0784 4.06274 13.75 5.68424 13.75 7.375V7.375Z" stroke="#E4E4E4"/>
                            </svg>
                            {searchValue && <img onClick={()=>setSearchValue('')} className='searchClose' src={'img/close.svg'} alt=''/>}
                            <input onChange={onSearchInp} value={searchValue} placeholder='Поиск...' style={{cursor: 'text'}} />
                        </div>
                    </div>
                    <div className='allCards'>
                        {renderItems()}
                    </div>
                </div>
            </section>
    )
}
export default Home;